import React from 'react';
import PropTypes from 'prop-types';
import {
  IconGithub,
  IconLinkedin,
  IconInstagram,
  IconTwitter,
  IconKaggle,
  IconMedium,
  IconAcclaim,
  IconDevTo,
} from '@components/icons';

const FormattedIcon = ({ name }) => {
  switch (name) {
    case 'Github':
      return <IconGithub />;
    case 'Linkedin':
      return <IconLinkedin />;
    case 'Instagram':
      return <IconInstagram />;
    case 'Twitter':
      return <IconTwitter />;
    case 'Kaggle':
      return <IconKaggle/>;
    case 'Medium':
      return <IconMedium/>;
    case 'YourAcclaim':
      return <IconAcclaim/>;
    case 'DevTo':
      return <IconDevTo/>;
    default:
      return <IconGithub />;
  }
};

FormattedIcon.propTypes = {
  name: PropTypes.string.isRequired,
};

export default FormattedIcon;
